body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background: #209cee;
  padding: 30px;
}
.app {
  height: 100%;
}

.todo-list {
  background: #e8e8e8;
  border-radius: 4px;
  padding: 5px;
  max-width: 400px;
  margin: 30px auto;
}

h1 {
  text-align: center;
  color: white;
}

.todo {
  background: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  padding: 8px 16px;
  font-size: 16px;
  margin-bottom: 6px;
  border-radius: 3px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.input {
  width: 100%;
  box-sizing: border-box;
  border: 0;
  font-size: 14px;
  border-radius: 4px;
  box-shadow: none;
  padding: 1rem;
  height: 3.5em;
}

button {
  border: none;
  border-radius: 3px;
  background: #fff;
  font-size: 18px;
  color: #666;
  margin: 0 1.5px;
  cursor: pointer;
}
p {
  cursor: pointer;
  margin: 0;
}

